.buttonClean {
  background-color: #cfb371;
  height: 40px;
  width: 100%;
  font-size: 16px;
  color: rgb(50, 49, 49);
  font-weight: bold;
  cursor: pointer;
  border: none;
  transition: all 0.3s ease;
}

.buttonClean:hover {
  background-color: rgb(50, 49, 49);
  color: rgb(207, 28, 28);
}
